/* Segmented Progress Bar */
.segmented-progress-container {
  margin-bottom: 20px;
}

.segmented-progress-bar {
  width: 100%;
  height: 48px;
  background: linear-gradient(135deg, #1a1a1a 0%, #2a2a2a 100%);
  border: 4px solid #27eaff;
  border-radius: 24px;
  display: flex;
  overflow: hidden;
  position: relative;
  box-shadow: 
    inset 0 2px 4px rgba(0,0,0,0.3),
    0 0 20px rgba(39,234,255,0.3),
    0 0 40px rgba(39,234,255,0.1);
  backdrop-filter: blur(10px);
}

.progress-segment {
  flex: 1;
  height: 100%;
  border-right: 2px solid rgba(255,255,255,0.1);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 900;
  color: white;
  text-shadow: 
    0 2px 8px rgba(0,0,0,0.8),
    0 0 10px rgba(255,255,255,0.3);
  overflow: hidden;
  backdrop-filter: blur(5px);
  letter-spacing: 0.5px;
  font-family: 'Orbitron', monospace;
}

.progress-segment::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  transition: left 0.6s ease;
}

.progress-segment:hover::before {
  left: 100%;
}

.progress-segment:last-child {
  border-right: none;
}

/* Kilit ikonu - üstte */
.segment-lock-icon {
  position: absolute;
  top: 8%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 12px;
  opacity: 0.9;
  z-index: 2;
  color: #fff;
  text-shadow: 0 1px 3px rgba(0,0,0,0.9);
  font-weight: bold;
}

/* Değer - altta */
.segment-value {
  position: absolute;
  bottom: 8%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 10px;
  opacity: 0.95;
  color: #fff;
  text-shadow: 0 1px 3px rgba(0,0,0,0.9);
  background: rgba(0,0,0,0.7);
  padding: 2px 4px;
  border-radius: 4px;
  border: 1px solid rgba(255,255,255,0.3);
  font-weight: 700;
  line-height: 1;
  white-space: nowrap;
  font-family: 'Orbitron', monospace;
  letter-spacing: 0.5px;
}

/* Completed segmentler için değer rengi */
.progress-segment.completed .segment-value {
  color: #fff;
  background: rgba(0,255,136,0.4);
  border: 1px solid rgba(0,255,136,0.6);
  text-shadow: 0 1px 3px rgba(0,0,0,0.9);
  font-weight: 800;
}

/* Available segmentler için değer rengi */
.progress-segment.available .segment-value {
  color: #fff;
  background: rgba(255,107,53,0.4);
  border: 1px solid rgba(255,107,53,0.6);
  text-shadow: 0 1px 3px rgba(0,0,0,0.9);
  font-weight: 800;
}

/* Locked segmentler için değer rengi */
.progress-segment.locked .segment-value {
  color: #fff;
  background: rgba(0,0,0,0.8);
  border: 1px solid rgba(255,255,255,0.2);
  text-shadow: 0 1px 3px rgba(0,0,0,0.9);
  font-weight: 700;
}

.progress-segment.completed {
  background: linear-gradient(135deg, #00ff88 0%, #00cc6a 50%, #00ff88 100%);
  background-size: 200% 200%;
  box-shadow: 
    inset 0 2px 4px rgba(0,0,0,0.3),
    0 0 15px rgba(0,255,136,0.6),
    0 0 30px rgba(0,255,136,0.3);
  border: 1px solid rgba(0,255,136,0.5);
}

.progress-segment.available {
  background: linear-gradient(135deg, #ff6b35 0%, #ff8c42 50%, #ff6b35 100%);
  background-size: 200% 200%;
  box-shadow: 
    inset 0 2px 4px rgba(0,0,0,0.3),
    0 0 15px rgba(255,107,53,0.6),
    0 0 30px rgba(255,107,53,0.3);
  border: 1px solid rgba(255,107,53,0.5);
  position: relative;
}

.progress-segment.available::after {
  display: none;
}

.progress-segment.locked {
  background: linear-gradient(135deg, #444 0%, #666 50%, #444 100%);
  background-size: 200% 200%;
  opacity: 0.7;
  cursor: not-allowed;
  border: 1px solid rgba(255,255,255,0.1);
  box-shadow: inset 0 2px 4px rgba(0,0,0,0.3);
  position: relative;
}



.progress-segment:hover.available {
  transform: scale(1.08) translateY(-3px);
  box-shadow: 
    0 12px 35px rgba(255,107,53,0.8),
    0 0 40px rgba(255,107,53,0.6),
    inset 0 2px 4px rgba(0,0,0,0.2);
  z-index: 10;
  border: 2px solid rgba(255,255,255,0.5);
}

.progress-segment:hover.completed {
  transform: scale(1.05) translateY(-2px);
  box-shadow: 
    0 8px 25px rgba(0,255,136,0.8),
    0 0 30px rgba(0,255,136,0.6),
    inset 0 2px 4px rgba(0,0,0,0.2);
  z-index: 10;
  border: 2px solid rgba(255,255,255,0.5);
}



.progress-percentage {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: #27eaff;
  font-weight: 900;
  font-size: 20px;
  text-shadow: 
    0 0 10px #27eaff,
    0 0 20px #27eaff,
    0 0 30px #27eaff;
  z-index: 10;
  font-family: 'Orbitron', monospace;
}

